* {
  margin: 0;
  font-family: 'Roboto', 'Helvetica', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  padding: 60px;
  line-height: 31px;
}

h1{
  font-size: 30px;
  font-weight: bolder;
}

h3{
  font-size: 25px;
  font-weight: bolder;
}

input{
  width: 100%;
  font-size: 1.3rem;
  display: block;
  margin: 10px auto;
  color: gray;
}

.category-result{
  margin-left: -30px;
  border-top: 1px dotted #ddd;
  margin-top: 20px;
  padding-top: 10px;

}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;

}
.card{
  align-content: center;
  background-color: #333;
  border:1px solid grey;
  border-radius: 6px;
  margin:10px;
  overflow: hidden;
  position: relative;
}

.card h4{
  color: #e5e5e5;
  padding: 5px;
  text-align: center;
}

.card img{
  width: 100%;
  max-height:200px;
}

